import axios from 'axios'
import qs from 'qs'

// //
axios.defaults.timeout = 5000;

if (process.env.VUE_APP_MODE !== 'production') {
    axios.defaults.baseURL = 'api';   //
} else {}
axios.defaults.withCredentials = true;

//POST()
axios.interceptors.request.use((config) => {
    //
    if(config.method === 'post' && config.headers['Content-Type']  === 'application/x-www-form-urlencoded;charset=UTF-8'){
        config.data = qs.stringify(config.data);
    }
    if (config.url === '/user/userModify' || config.url === '/uploader/mergeFile') {
        config.headers['token'] = localStorage.getItem('token')
    }
    if (config.url === '/findpassword/update') {
        config.headers['token'] = sessionStorage.getItem('tmptoken')
        sessionStorage.removeItem('tmptoken')
    }
    return config;
},(error) =>{
    console.log('')
    return Promise.reject(error);
});

//()
axios.interceptors.response.use((res) =>{
    //
    if(!res.data.success){
        return Promise.resolve(res);
    }
    return res;
}, (error) => {
    console.log('')
    return Promise.reject(error);
});

//Promise(post)
export function fetchPost(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'}})
            .then(response => {
                resolve(response);
            }, err => {
                reject(err);
            })
            .catch((error) => {
                reject(error)
            })
    })
}

//Promise(post, json)
export function fetchJsonPost(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}})
            .then(response => {
                resolve(response);
            }, err => {
                reject(err);
            })
            .catch((error) => {
                reject(error)
            })
    })
}


////Promise(get)
export function fetchGet(url, param) {
    return new Promise((resolve, reject) => {
        axios.get(url, {params: param})
            .then(response => {
                resolve(response)
            }, err => {
                reject(err)
            })
            .catch((error) => {
                reject(error)
            })
    })
}

export default {
    fetchPost,
    fetchGet,
    fetchJsonPost
}
