import Vue from 'vue'
import VueRouter from 'vue-router'
import Header from "@/components/Header.vue";
import Login from "@/views/Login.vue";
import index from "@/views/index.vue";
import About from '@/views/About.vue'
import Retrieve from '@/views/retrieve.vue'
import Register from '@/views/Register.vue'
import UserInfo from '@/views/userinfo/UserInfo.vue'
import basicInfo from '@/views/userinfo/basicInfo.vue'
import changeinfo from '@/views/userinfo/changeinfo.vue'
import changemail from '@/views/userinfo/changeinfo.vue'
import changephone from '@/views/userinfo/changephone.vue'
import contribute from '@/views/userinfo/contribute.vue'
import favourition from '@/views/favourition.vue'
import uploader from "@/views/uploader";
import VideoModify from "@/views/VideoModify";

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'index',
    component: index
  },
    {
      path: '/login',
      name: 'Login',
      component: Login
      },
    {
      path: '/retrieve',
      name: 'Retrieve',
      component: Retrieve
    },
    {
      path: '/favourition',
      name: 'favourition',
      component: favourition
    },
    {
      path: '/videomodify',
      name: 'VideoModify',
      component: VideoModify
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/userinfo',
      name: 'userinfo',
      redirect:'userinfo/basicinfo',
      component: UserInfo,
      children: [
        {
          path: 'basicinfo',////;pathpathpath
          name: 'basicinfo',
          component: basicInfo
        },
        {
          path: 'changeinfo',////;pathpathpath
          name: 'changeinfo',
          component: changeinfo
        },
        {
          path: 'changephone',////;pathpathpath
          name: 'changephone',
          component: changephone
        },
        {
          path: 'changemail',////;pathpathpath
          name: 'changemail',
          component: changemail
        },
        {
          path: 'contribute',////;pathpathpath
          name: 'contribute',
          component: contribute
        }
      ]
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/uploader',
      name: 'uploader',
      component: uploader
    }
]

const router = new VueRouter({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes
})



export default router
