<template>
<div  class="Cbody_item">
                        <div class="mainInfo">
                        <span id="headIcon">
                            <img   width=80px height=80px :src="headiconurl">
                        </span>
                         <span class="accountInfo">
                            <div id="username">
                             {{username}}
                            </div>
                             <div id="useraccount" class="subInfo">
                                
                                <span>{{accountNum}}</span>
                            </div>
                            <div id="usersign" class="subInfo">
                                <span>{{usersign}}</span>
                            </div>
                        </span>
                        <div>
                            <router-link :to="{name: 'changeinfo'}" id="changeInfo-button" ></router-link>
                        </div>
                        </div>
                        <div >
                        <span class="beforeline"></span>
                        <span >
                        <hr class="cutline">
                        </span>
                        </div>
                        <div class="mainInfo">
                         <span >
                             <span id="left">
                            <div id="usersex" class="basicInfo">
                                <span>{{usergender}}</span>
                            </div>
                            <div id="userage" class="basicInfo">
                                <span>{{userage}}</span>
                            </div>
                            <div id="userage" class="basicInfo">
                                <span>{{userbirth}}</span>
                            </div>
                             </span>
                            <span id="right">
                             <div id="userloc" class="basicInfo">
                                <span>{{userloc}}</span>
                            </div>
                             <div id="userlcol" class="basicInfo">
                                <span>{{usercol}}</span>
                            </div>
                            <div id="userlcol" class="basicInfo">
                                <span></span>
                            </div>
                            </span>
                        </span>
                        </div>
                         <span class="beforeline"></span>
                         <hr class="cutline">
                          <div class="mainInfo">
                          <span class="basicInfo">
                             <span id="left">
                            <div id="userphone" class="basicInfo">
                                <span>{{userphone}}</span> <router-link :to="{name: 'changephone'}" id="changebind" ></router-link>
                            </div>

                             </span>
                            <span id="right">
                             <div id="useremail" class="basicInfo">
                                <span>{{useremail}}</span><router-link :to="{name: 'changemail'}" id="changebind" ></router-link>
                            </div>
                            </span>
                        </span>
                        </div>

                         <span class="beforeline"></span>
                         <hr class="cutline">
                        </div>
</template>

<script>
    export default {
        name: "basicinfo",
        data() {
            return {
                username:"",
                accountNum:631570777,
                usersign: "",
                userage:9,
                userloc:"",
                usergender:"",
                usercol:"",
                useremail:"631570777@qq.com",
                userphone:10086,
                userbirth:"184097",
                cur: 0, //tab,
                headiconurl: '',
            }
        },
        mounted() {
            this.getUserInfo()
        },
        methods:{
            getUserInfo() {
                let userinfo = JSON.parse(localStorage.getItem('userinfo'))
                console.log(userinfo)
                this.username = userinfo.userName
                this.usergender = userinfo.userGender === 'male' ? '' : ''
                this.userbirth = userinfo.userBirthday
                this.useremail = userinfo.userEmail
                this.userphone = userinfo.userPhoneNumber
                if (process.env.VUE_APP_MODE !== 'production') {
                    this.headiconurl = 'api'+userinfo.image
                } else {
                    this.headiconurl = userinfo.image
                }

                this.usersign = userinfo.userSignature
            }
        }
    }

</script>

<style  scoped lang = "scss" src=@/assets/scss/userinfo.scss >

</style>
